@value Variables: "../../../styles/Variables.css";
@value fontFam-1,defaultFont, black-color, tertiary-color from Variables;
.Card {
  height: 26vh;
  min-height: 22rem;
  width: 100%;
  overflow: auto;
  border-radius: 8px;
  box-shadow: 0 0.5rem 2rem rgba(14, 14, 14, 0.1);
  position: relative;
  background: #fff;
  color: #000;
  font-weight: bold;
  font-family: fontFam-1;
  border-bottom: 2px solid #eb1761;
  border-bottom: 2px solid var(--color1);
}

.Card:hover {
  cursor: pointer;
  /* background:#fafafa; */
  box-shadow: 0px 0px 10px #aaa;
}
.Card:active {
  cursor: pointer;
  box-shadow: inset 0px 0px 10px #aaa;
}

.Body {
  width: 100%;
  height: 19vh;
}

.Text {
  text-align: center;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.Text .status {
  font-weight: bold;
  font-size: 14px;
  color: #999;
}

.Text .live {
  color: var(--color1);
  border: 1px solid var(--color3);
  padding: 2px 10px;
  border-radius: 2px;
}

.Footer {
  height: 4vh;
  width: 100%;
  position: absolute;
  bottom: 0.4rem;
  text-align: center;
  border-top: 1px solid black-color;
}

.FooterText {
  font-size: 1.3rem;
  background-color: tertiary-color;
  color: black-color;
  border-radius: 9px;
  cursor: pointer;
  outline: none;
  display: inline-block;
  padding: 0.2rem 1.2rem;
  margin-top: 0.5rem;
}

.closeBtn {
  position: absolute;
  top: 20px;
  right: 20px;
}

.closeBtn {
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 2.5rem;
  height: 2.5rem;
  box-shadow: 0 0 5px #000;
  text-align: center;
  border-radius: 50%;
}
.closeBtnVisibility {
  display: none;
}
.closeBtn:hover {
  box-shadow: 0 0 10px #000;
}

.closeIcon {
  color: #e61f00;
  font-size: 25px;
}
