/**************Modal Styles***************/
.modal_container,
.modal_container2,
.modal_container3 {
  z-index: 100000000000000000000;
  -webkit-transition: all 0.3s cubic-bezier(0.4, 0, 0, 1);
  transition: all 0.3s cubic-bezier(0.4, 0, 0, 1);
}

.modal_container.open,
.modal_container2.open,
.modal_container3.open {
  opacity: 1;
  visibility: visible;
}

.modal_body {
  position: absolute;
  top: 40%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: #fff;
  border-radius: 10px;
  -webkit-transition: all 0.3s cubic-bezier(0.4, 0, 0, 1);
  transition: all 0.3s cubic-bezier(0.4, 0, 0, 1);
}

.half {
  width: 50vw;
}

.full {
  width: 70vw;
}

.modal_container.open .modal_body,
.modal_container2.open .modal_body,
.modal_container3.open .modal_body {
  opacity: 1;
  visibility: visible;
  top: 50%;
  -webkit-transition-delay: 0.1s;
          transition-delay: 0.1s;
}

.close_modal {
  font-size: 1.4em;
  cursor: pointer;
  position: absolute;
  top: 50%;
  right: 1em;
  padding: 0.5em;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.scroll_body,
.scroll_body2 {
  float: left;
  width: 100%;
