  overflow-y: scroll;
  margin-bottom: 2em;
}
.table-wrapper {
  width: 100%;
  float: left;
}

.fl-table {
  border-radius: 5px;
  font-size: 12px;
  font-weight: normal;
  border: none;
  border-collapse: separate;
  border-spacing: 0 1.5em;
  width: 100%;
  max-width: 100%;
  white-space: nowrap;
}

.fl-table td,
.fl-table th {
  text-align: left;
  padding: 0.8em 1em;
  position: relative;
  background: white;
}

.fl-table tr {
  position: relative;
  border-radius: 50px;
}

.fl-table tbody td {
  cursor: pointer;
  -webkit-transition: all 0.3s cubic-bezier(0.4, 0, 0, 1);
  transition: all 0.3s cubic-bezier(0.4, 0, 0, 1);
  background: white;
}

.fl-table tbody td:nth-child(1) {
  padding-left: 1em;
}

.fl-table tbody tr:hover {
  color: var(--blue);
}

.fl-table tr:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(13, 110, 255, 0.05);
  box-shadow: 0 0 0 1px var(--blue);
  opacity: 0;
  -webkit-transition: all 0.3s cubic-bezier(0.4, 0, 0, 1);
  transition: all 0.3s cubic-bezier(0.4, 0, 0, 1);
  border-radius: 10px;
  z-index: 1000;
  pointer-events: none;
}

.fl-table tbody tr:hover:before {
  opacity: 1;
}
.fl-table th:nth-child(1),
.fl-table td:nth-child(1) {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  padding-left: 1em;
}

.fl-table th:nth-last-child(1),
.fl-table td:nth-last-child(1) {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.fl-table td {
  border-right: none;
  font-size: 1.1em;
  padding: 1.5em 1em;
}

